@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800&display=swap);
/* RESET */
* {
  box-sizing: border-box;
  border: none;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Montserrat', sans-serif;
}

.container {
  margin: auto;
  max-width: 1400px;
  min-height: 100vh;
  padding: 3rem 0;
}

.main {
  text-align: center;
}

.main p {
  font-weight: 500;
  font-size: 2.5rem;
}

#about {
  font-weight: 700;
}

.main-wrap {
  position: relative;
  text-align: left;
  left: 50%;
  -webkit-transform: translateX(-20%);
          transform: translateX(-20%);

  margin-top: 3rem;
  line-height: 9rem;
}

.main-wrap div {
  font-size: 10rem;
  letter-spacing: -1.4rem;
}

.main-wrap span {
  font-weight: 600;
}

.bridge {
  cursor: pointer;
}

/* ABOUT */
#about:after {
  content: 'Wonseok';
  letter-spacing: -0.1rem;
}

#about:hover:after {
  color: #03e9f4;
  content: 'ABOUT';
  transition: all 0.5s;
}

/* CONTACT */
#contact:after {
  content: 'make';
}

#contact:hover::after {
  color: #03e9f4;
  content: 'CONTACT';
  transition: all 0.5s;
}

/* PROJECTS */
#projects:after {
  content: 'experience';
}

#projects:hover::after {
  color: #03e9f4;
  content: 'PROJECTS';
  transition: all 0.5s;
}

nav {
  display: flex;
  width: 80%;
  margin: auto;
  margin-bottom: 3rem;

  align-items: center;
}

.nav-left,
.nav-right {
  width: 50%;
}

.nav-right {
  display: flex;
  justify-content: right;
  margin-right: 0.5rem;
}

.nav-right button {
  margin-left: 1rem;
}

.btn {
  font-weight: 500;
  background: transparent;
  cursor: pointer;
}

.btn-home {
  font-size: 2rem;
  font-weight: 700;
}

.nav-active {
  font-size: 1.2rem;
  font-weight: 800;
}

/* Animation */
.btn:hover {
  -webkit-animation: effectY 0.3s ease-in-out;
          animation: effectY 0.3s ease-in-out;
}

@-webkit-keyframes effectY {
  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes effectY {
  50% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

a {
  text-decoration: none;
  color: #fff;
}
footer {
  width: 100%;
  text-align: center;
  padding: 5rem 0 3rem 0;
  margin-top: 4rem;

  color: #fff;
  background: rgb(0, 0, 0);
}

.footer-wrap {
  width: 70%;
  margin: auto;
}

.footer-wrap h2 {
  font-size: 1rem;
}

.fab {
  margin: 1rem 0.5rem;
}

a:hover {
  color: #03e9f4;
}

footer h3 {
  font-size: 0.8rem;
}

i:hover {
  color: #03e9f4;
}

main {
  padding: 0 6rem;
}

.title {
  font-size: 3.2rem;
  margin-bottom: 2rem;
}

/* section: myself */
.myself {
  display: flex;
  width: 100%;
  margin-bottom: 5rem;
}

.myself-left {
  width: 60%;
  padding-right: 8rem;
}

.myself-left p {
  font-size: 1.3rem;
}

.myself-right {
  width: 40%;
}

.myself-img {
  width: 100%;
  height: 30rem;
  background-position: center;
  vertical-align: bottom;
  border-radius: 1rem;
}

/* section: SKILLS */
.skills-wrap {
  margin-top: 3rem;
}

.skills-wrap span {
  padding: 0.5rem;
  border: 3px black solid;
  border-radius: 0.6rem;

  font-size: 1.2rem;
  font-weight: 600;

  margin-right: 1rem;
}

.contact-greeting {
  text-align: center;
}

.contact-greeting p {
  width: 90%;
  margin: auto;
  font-size: 1.3rem;
}

/* Section: FORM */
.contact-wrap {
  margin: auto;
  width: 90%;
  margin-top: 2.5rem;
  padding: 2rem;
  padding-bottom: 4rem;
  border-radius: 1rem;
  background: linear-gradient(#031c49, #243b55);
}

.gform {
  width: 100%;
}

.form-elements {
  position: relative;
}

.form-personal {
  display: flex;
}

.form-value {
  width: 100%;
  font-size: 1.4rem;
  padding: 0.5rem 0;
  padding-left: 1rem;
  margin-bottom: 0.5rem;

  border: none;
  border-bottom: 2px solid #000;
  background: transparent;
  outline: none;

  color: #fff;
}

#name {
  margin-right: 8rem;
}

#email {
  margin-right: 18rem;
  margin-left: 2rem;
}

.form-value:focus {
  border-bottom: 2px solid #03e9f4;
}

.btn-submit {
  position: absolute;
  right: 0;
  cursor: pointer;

  padding: 0.5rem 2rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  border-radius: 0.5rem;
  background: transparent;
}

.thankyou_message {
  color: #03e9f4;
  font-size: 2rem;
}

/* Animation */
.btn-submit:hover,
.btn-submit:active {
  -webkit-animation: effectBg 1s forwards;
          animation: effectBg 1s forwards;
}

@-webkit-keyframes effectBg {
  100% {
    background: #03e9f4;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
      0 0 100px #03e9f4;
  }
}

@keyframes effectBg {
  100% {
    background: #03e9f4;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
      0 0 100px #03e9f4;
  }
}

.projects {
  text-align: center;
}

.project-wrap {
  display: flex;
  margin-top: 4rem;
}

.project-img__wrap {
  position: relative;
  margin-right: 10rem;
  width: 520px;
  height: 320px;
}

.laptop-img {
  position: absolute;
  left: 0;
  top: 0;
  width: 520px;
  height: 320px;
}

.project-img {
  position: absolute;
  left: 73px;
  top: 32px;
  width: 374px;
  height: 240px;
}

.project-info__wrap {
  display: flex;
  width: 500px;
  flex-direction: column;
  padding: 1rem 4rem;
}

.project-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.project-skills__wrap {
  margin-bottom: 4rem;
}

.project-skills__wrap span {
  font-size: 1.4rem;
  border: 3px solid black;
  border-radius: 0.3rem;
  padding: 0.2rem 0.5rem;
  margin-right: 0.5rem;
}

.project-btns {
  font-size: 1.4rem;
  font-weight: 600;
  padding: 0.4rem 0;

  border: none;
  border-radius: 1rem;

  cursor: pointer;
  margin-bottom: 1rem;
}

.live-btn {
  color: #fff;
}

.code-btn {
  padding: 5px 0;
  background: #fff;
}

/* Animation */
.project-btns:hover {
  opacity: 0.6;
  -webkit-animation: effectX 0.3s ease-in-out;
          animation: effectX 0.3s ease-in-out;
}

@-webkit-keyframes effectX {
  50% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes effectX {
  50% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

